<template>
<!--     <div class="container"> -->
        <div class="container">
            <div class="header">
        <div class="home-menu pure-menu pure-menu-horizontal pure-menu-fixed">
            <a class="pure-menu-heading" href="/">
                 公共英语听说</a>

      
            <li class="pure-menu-item pure-menu-selected">
                <router-link :to="{name:'coursejsLink'}" class="pure-menu-link">课程建设 </router-link>
            </li>
            <li class="pure-menu-item pure-menu-selected">
                <router-link :to="{name:'courseszLink'}" class="pure-menu-link">课程设置 </router-link>
            </li>
            <li class="pure-menu-item pure-menu-selected">
                 <router-link :to="{name:'pptlist'}" class="pure-menu-link">课程教案 </router-link>
             </li>

               <li class="pure-menu-item pure-menu-selected pure-menu-has-children pure-menu-allow-hover">
                <a href="#" class="pure-menu-link">在线题库</a>
                <ul class="pure-menu-children">
                <li class="pure-menu-item"><a href="/dhu/tests/list" class="pure-menu-link" style="color:#6699CC">listening</a></li>
                <li class="pure-menu-item"><a href="/dhu/read/list" class="pure-menu-link" style="color:#6699CC">reading</a></li>
              
            </ul>
            </li>
          <li class="pure-menu-item pure-menu-selected">
                 <router-link :to="{name:'forum'}" class="pure-menu-link">论坛</router-link>
             </li>
            <li class="pure-menu-item pure-menu-selected"><a href="/dhu/video" class="pure-menu-link">教学录像</a></li>
            <!-- <li class="pure-menu-item pure-menu-selected"><a href="#" class="pure-menu-link">校内外评估</a></li> -->
            <!-- <li class="pure-menu-item pure-menu-selected"><a href="#" class="pure-menu-link">科研成果</a></li> -->
          
             <ul class="pure-menu-list text-right">
              <li class="pure-menu-item" v-show="!logged" > <el-button size="small"  v-on:click="dialogFormVisibleLogin = true">log in</el-button></li>
              <li class="pure-menu-item" v-show="!logged" > <el-button size="small"  v-on:click="dialogFormVisibleRegister = true">register</el-button></li>
                <li class="pure-menu-item" v-show="logged" style="color:white;">当前用户：{{username}}&nbsp;&nbsp;&nbsp;&nbsp;</li>
                <li class="pure-menu-item" v-show="logged" > <el-button size="small"  @click="loginout">log out</el-button></li>
            </ul>
    </div>
    </div>
<el-dialog title="登录" :visible.sync="dialogFormVisibleLogin">
    <el-form
      :model="ruleForm2"
      status-icon
      :rules="rules2"
      ref="ruleForm2"
      label-width="30%"
      class="demo-ruleForm"
      v-loading="ruleForm2.user_loading"
    >
      <el-form-item label="用户名" prop="user_username">
        <el-input v-model.number="ruleForm2.user_username" style="width:60%"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="user_password">
        <el-input
          type="password"
          v-model="ruleForm2.user_password"
          autocomplete="off"
          style="width:60%"
        ></el-input>
      </el-form-item>
    </el-form>

    <div slot="footer" class="dialog-footer">
      <div class="mycontainer3">
        <el-button @click="resetForm('ruleForm2')">重置</el-button>
        <el-button @click="dialogFormVisibleLogin = false;ruleForm2.user_loading=false">取消</el-button>
        <el-button type="primary" @click="submitLogin('ruleForm2')">提交</el-button>
      </div>
    </div>
  </el-dialog>

  <el-button size="small"  @click="dialogFormVisibleRegister = true">注册</el-button>

  <el-dialog title="注册" :visible.sync="dialogFormVisibleRegister">
  <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="30%" class="demo-ruleForm" >
  <el-form-item label="用户名" prop="user_username" >
    <el-input v-model="ruleForm2.user_username" style="width:60%"></el-input>
  </el-form-item>
  <el-form-item label="密码" prop="user_password">
    <el-input type="password" v-model="ruleForm2.user_password" autocomplete="off"  style="width:60%"></el-input>
  </el-form-item>
  <el-form-item label="确认密码" prop="user_checkpassword">
    <el-input type="password" v-model="ruleForm2.user_checkpassword" autocomplete="off"  style="width:60%"></el-input>
  </el-form-item>

  <el-form-item label="手机号码" prop="user_phone" >
    <el-input v-model.number="ruleForm2.user_phone"  style="width:60%"></el-input>
  </el-form-item>
  <el-form-item label="邮箱" prop="user_email" >
    <el-input v-model="ruleForm2.user_email"  style="width:60%"></el-input>
  </el-form-item>
  </el-form>
  
  <div slot="footer" class="dialog-footer" >
    <div class="mycontainer3">
    <el-button @click="resetForm('ruleForm2')">重置</el-button>
    <el-button  @click="dialogFormVisibleRegister = false;ruleForm2.loading=false">取 消</el-button>
    <el-button type="primary" @click="submitRegister('ruleForm2')">提交</el-button>
    </div>
  </div>
</el-dialog>
    </div>
    
</template>


<script scoped>
/*$webfont.load(".header", "8046d7d9695546e7a652959e05482a9b", "Tianshi-Yanti"); */
/*$webfont.load("#id1,.class1,h1", "8046d7d9695546e7a652959e05482a9b", "Tianshi-Yanti");*/
/*$webfont.draw();*/
export default {
    data() {
        
    var checkName = (rule, value, callback) => {
      if (!value) {
          this.judge_username=false;
        return callback(new Error("用户名不能为空"));
      } else {
           this.judge_username=true;
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
        this.judge_password=false;
      } else {
        if (this.ruleForm2.checkPass !== "") {
          this.$refs.ruleForm2.validateField("checkPass");
          this.judge_password=false;
        }
        this.judge_password=true;
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
        this.judge_checkpassword=false;
      } else if (value !== this.ruleForm2.user_password) {
        callback(new Error("两次输入密码不一致!"));
        this.judge_checkpassword=false;
      } else {
        callback();
        this.judge_checkpassword=true;
      }
    };
    var checkPhonenumber = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入手机号码"));
        this.judge_phone=false;
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error("请输入数字值"));
          this.judge_phone=false;
        } else {
          if (value.toString().length != 11) {
            callback(new Error("请输入11位手机号码"));
            this.judge_phone=false;
          } else {
            callback();
            this.judge_phone=true;
          }
        }
      }, 1000);
    };
    var checkEmail = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入邮箱"));
        this.judge_email=false;
      }
      setTimeout(() => {
        var reg = new RegExp(
          "^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$"
        ); //正则表达式
        if (!reg.test(value)) {
          callback(new Error("请输入正确格式的邮箱"));
          this.judge_email=false;
        } else {
          callback();
          this.judge_email=true;
        }
      }, 1000);
    };
    return {
        logged:false,
        username:'',
      dialogFormVisibleLogin: false,
      dialogFormVisibleRegister: false,
      ruleForm2: {
        user_username: "",
        user_password: "",
        user_checkpassword: "",
        user_phone: "",
        user_email: "",
        user_loading: false
      },
      rules2: {
        user_password: [{ validator: validatePass, trigger: "blur" }],
        user_checkpassword: [{ validator: validatePass2, trigger: "blur" }],
        user_username: [{ validator: checkName, trigger: "blur" }],
        user_phone: [{ validator: checkPhonenumber, trigger: "blur" }],
        user_email: [{ validator: checkEmail, trigger: "blur" }]
      },
      active:0,
      active_login:0,
      gridData: [],
      judge_password: false,
      judge_checkpassword: false,
      judge_username:false,
      judge_phone:false,
      judge_email:false
    };
  },
  methods: {
    btlogin() {
      console.log("login");
    },
    btregister() {
      console.log("register");
    },
    //重置表格
    resetForm(formName) {
      this.ruleForm2={};
      this.$refs[formName].resetFields();
    },
    submitLogin(formName) {
      this.ruleForm2.user_loading = true;
      // this.getStatusLogin(this.submitFormLogin,formName);   //入口
      var tp = this;
      var params = new URLSearchParams();
      params.append("user_username", this.ruleForm2.user_username);
      params.append("user_password", this.ruleForm2.user_password);
      this.$http({
        method: "post",
        url: "http://47.96.87.249:8080/dhuf/user_login",
    //   url: "http://192.168.1.107:8080/dhu/user_login",
        params
      }).then(function(response) {
        //收到回复，取消转圈圈图标
        tp.ruleForm2.user_loading = false;
        console.log("response.data:" + response.data);
        var msg = response.data;
        var obj = JSON.parse(msg[0]);
        console.log(obj.status);
        if (obj.status == "Success") {
          tp.$message({
            message: "登录成功",
            type: "success"
          });
          tp.dialogFormVisibleLogin = false;
          tp.username = tp.ruleForm2.user_username;
          tp.logged=true;
          tp.refresh();
        }else if(obj.status=="AlreadyLogin"){
          tp.$message({
                message: "您已登录该用户",
                type: "warning"
            });
        }else if(obj.status=="PleaseLoginOut"){
          tp.$message({
            message:"请先退出登录再登录其他用户",
            type:"warning"
          })
        }else if(obj.status=="AlreadyLoginOther"){
          tp.$message({
            message:"此用户已在其他地方登录",
            type:"warning"
          })
        }else if(obj.status=="NoUser"){
            tp.$message({
                message: "没有该用户",
                type: "warning"
            })
        }else if(obj.status=="Fail"){
            tp.$message({
                message: "密码错误",
                type: "warning"
            })
        }else {
            tp.$message({
                message:"后台出错",
                type: "error"
            })
        }
      });
      console.log("");
    },
    submitRegister(formName){
        this.ruleForm2.user_loading = true;
    var jud = this.judge_checkpassword&&this.judge_username&&this.judge_password&&this.judge_email&&this.judge_phone;
    if(!jud){
        return;
    }
        var tp = this;
        var params=new URLSearchParams();
        params.append("user_username",this.ruleForm2.user_username);
        params.append("user_password",this.ruleForm2.user_password);
        params.append("user_note",this.ruleForm2.user_note);
        params.append("user_phone",this.ruleForm2.user_phone);
        params.append("user_email",this.ruleForm2.user_email);
        this.$http({
          method:"post",
          url:"http://47.96.87.249:8080/dhuf/user_register",
        // url:"http://192.168.1.107:8080/dhu/user_register",
          params
        }).then(function(response){
          tp.ruleForm2.user_loading=false;
          console.log("response.data:" + response.data);
          var msg=response.data;
          var obj=JSON.parse(msg[0]);
          console.log(obj.status);
          if(obj.status=="Success"){
            tp.$message({
              message:"注册成功",
              type:"success"
            });
            tp.dialogFormVisibleRegister=false;
          }else if(obj.status=="Exist"){
            tp.$message({
              message:"用户名已存在",
              type:"warning"
            });
            
          }else{
            tp.$message({
              message:"后台出错",
              type:"error"
            });
           
          }
        });
    },
    check(){
      var tp=this;
      this.$http({
        method:"post",
        url:"http://47.96.87.249:8080/dhuf/user_test"
      //  url:"http://192.168.1.107:8080/dhu/user_test"
      }).then(function(response){
        var msg=response.data;
        var obj=JSON.parse(msg[0]);
        console.log("now:"+obj.status);
        if(obj.status=="have"){
          tp.$message({
              offset:200,
            message:"已经登录，用户名："+obj.name,
            type:"success"
          });
          tp.logged=true;
          tp.username=obj.name;
        }else if(obj.status=="no"){
          tp.$message({
               offset:200,
            message:"未登录",
            type:"warning"
          });
        }else {
          tp.$message({
               offset:200,
            message:"后台出错",
            type:"error"
          });
        }
      });
    },


    //登出，登出会先请求登录状态获得此浏览器用户名之后再登出
    async loginout(){
      var tp=this;
      var params=new URLSearchParams();
      var response=await this.$http({
        method:"post",
        url:"http://47.96.87.249:8080/dhuf/user_test"
   //     url:"http://192.168.1.107:8080/dhu/user_test"
      });
      var msg=response.data;
      var obj=JSON.parse(msg[0]);
      if(obj.status=="have"){
      params.append("user_username",obj.name);
      this.$http({
        method:"post",
        url:"http://47.96.87.249:8080/dhuf/user_loginout"
    //    url:"http://192.168.1.107:8080/dhu/user_loginout"
      }).then(function(response){
        var msg=response.data;
        var obj=JSON.parse(msg[0]);
        console.log("now_logout_:"+obj.status);
        if(obj.status=="Success"){              //取消登录成功
          tp.$message({
               offset:200,
            message:"登出成功，用户名："+obj.name,
            type:"success"
          });
          tp.logged=false;
          tp.refresh();
        }else if(obj.status=="Fail"){
          tp.$message({
               offset:200,
            message:"该用户没有处于登录状态",
            type:"warning"
          });
        }else{
          //即status=Error
          tp.$message({
               offset:200,
            message:"后台出错",
            type:"error"
          });
        }
      });
      }else if(obj.status=="no"){
        tp.$message({
             offset:200,
          message:"该用户没有处于登录状态",
          type:"warning"
        })
      }else{
        tp.$message({
             offset:200,
          message:"后台出错",
          type:"error"
        })
      }
    },
    refresh(){
      var tp=this;
      this.$http({
        method:"post",
        url:"http://47.96.87.249:8080/dhuf/user_gtallstatus"
     //   url:"http://192.168.1.107:8080/dhu/user_gtallstatus"
      }).then(function(response){
        var msg=response.data;
        var obj=JSON.parse(msg[0]);
        var active=obj.active;
        var active_login=obj.active_login;
        console.log("active:"+active+"--active_login:"+active_login);
        tp.active=active
        tp.active_login=active_login
      })
    }
    ,
    gtactive(){
      var tp=this;
      this.$http({
        method:"post",
        url:"http://47.96.87.249:8080/dhuf/user_gtallstatus"
     //   url:"http://192.168.1.107:8080/dhu/user_gtallstatus"
      }).then(function(response){
        var msg=response.data;
        var obj=JSON.parse(msg[0]);
        var active=obj.active;
        var active_login=obj.active_login;
        tp.$message({
             offset:200,
          message:"刷新成功",
          type:"success"
        });
        tp.active=active
      })
    },
    gtactive_login(){
      var tp=this;
      this.$http({
        method:"post",
        url:"http://47.96.87.249:8080/dhuf/user_gtallstatus"
    //    url:"http://192.168.1.107:8080/dhu/user_gtallstatus"
      }).then(function(response){
        var msg=response.data;
        var obj=JSON.parse(msg[0]);
        var active=obj.active;
        var active_login=obj.active_login;
        tp.$message({
             offset:200,
          message:"刷新成功",
          type:"success"
        });
        tp.active_login=active_login
      })
    },
    gtactive_user(){
      this.gridData.splice(0,this.gridData.length);
      var tp=this;
      this.$http({
        method:"post",
        url:"http://47.96.87.249:8080/dhuf/user_gtonlineusers"
    //    url:"http://192.168.1.107:8080/dhu/user_gtonlineusers"
      }).then(function(response){
        var msg=response.data;
        var obj=JSON.parse(msg[0]);
        var status=obj.status;
        if(status=="Fail"){
          tp.$message({
               offset:200,
            message:"没有权限查看或未登录管理员账户",
            type:"warning"
          });
        }else if(status=="Error"){
          tp.$message({
               offset:200,
            message:"后台出错",
            type:"warning"
          });
        }else if(status=="Success"){
          var mydata_str=obj.mydata;
          var mydata=JSON.parse(mydata_str);
          
          for(var x in mydata){
            var obj1=mydata[x];
            tp.gridData.splice(0,0,{
              name:obj1
            })
          }
        }else{
          tp.$message({
               offset:200,
            message:"其他错误",
            type:"error"
          })
        }
        
      });
    }
  },
  created(){
    console.log("running created");
    this.gtactive();
    this.gtactive_login();
    this.check();
  }  
}
</script>

<style scoped>
    @import "../css/marketing.css";
    img{
    width: 30px;
    height: 30px;
}
</style>



