<template>
  <div class="container">
  <el-table
    :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%"
  >
    <el-table-column label="日期" prop="docu_data"></el-table-column>
    <el-table-column label="名字" prop="docu_name"></el-table-column>
    <el-table-column label="类别" prop="docu_lei"></el-table-column>
     <el-table-column label="大小" prop="docu_sz"></el-table-column>
    <el-table-column align="right">
      <template slot="header">
        <el-input v-model="search" size="mini" placeholder="输入关键字搜索"/>
      </template>
      <template slot-scope="scope">
        <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">下载</el-button>
      </template>
    </el-table-column>
  </el-table>
  <br>

  <el-pagination class="text-right"
    background
    layout="prev, pager, next"
    @size-change="sizechange"
    @current-change="currentchange"
    :page-size="pagesize"
    :total="total"
    :current-page="currentpage"
  ></el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      search: "",
      total: 0,
      pagesize: 5,
      currentpage: 2
    };
  },
  methods: {
    async handleEdit(index, row) {
      var params = new URLSearchParams();
      params.append("docu_name", row.docu_name);
      var response = await this.$http({
        method: "get",
        url: "http://47.96.87.249:8080/dhuf/docu_isexist",
       //   url: "http://localhost:8080/dhu/docu_isexist",
        params
      });
      var msg = response.data;
      var obj = JSON.parse(msg[0]);
      console.log(obj.status);
      if (obj.status == "success") {
        location.href =
          "http://47.96.87.249:8080/dhuf/docu_download?docu_name=" + row.docu_name;
         //   "http://localhost:8080/dhu/docu_download?docu_name="+row.docu_name;
        this.$message({
          type: 'success',
          message: '下载成功'
        })
      } else {
        this.$message({
          type: 'error',
          message: '没有该文件哦'
        })
      }
      this.updatepage();
      this.changeinfo();
    },
    buquan(year, month, day) {
      var yearstr = year.toString();
      var monthstr = month.toString();
      var daystr = day.toString();
      if (monthstr.length == 1) monthstr = "0" + monthstr;
      if (daystr.length == 1) daystr = "0" + daystr;
      return yearstr + "-" + monthstr + "-" + daystr;
    },
    updatepage() {
      var tp = this;
      var params = new URLSearchParams();
      params.append("docu_suffix","ppt_word_pdf");  //每个需要的文件后缀通过下划线分开
      this.$http({
        method: "post",
        url: "http://47.96.87.249:8080/dhuf/docu_gecount_suf",
    //    url: "http://localhost:8080/dhu/docu_gecount_suf",
        params
      }).then(function(response) {
        if (response.status == 200) {
          var msg = response.data;
          var obj = JSON.parse(msg[0]);
          tp.total = obj.result;
        }
      });
    },
    sizechange(val) {
      console.log("size change");
      console.log(`每页 ${val} 条`);
    },
    currentchange(val) {
      this.currentpage = val;
      this.$router.push({ path: "/ppt/list", query: { page: val } });
      console.log(this.$route.query.page);
      this.updatepage();
      this.changeinfo();
    },
    changeinfo() {
      var tp = this;
      var params = new URLSearchParams();
      params.append("docu_suffix","ppt_word_pdf");
      params.append("begin", (this.currentpage - 1) * this.pagesize);
      params.append("pageSize", this.pagesize);
      this.$http({
        method: "post",
        url: "http://47.96.87.249:8080/dhuf/docu_geDocu_suf",
    //      url: "http://localhost:8080/dhu/docu_geDocu_suf",
        params
      }).then(function(response) {
        var msg = response.data;
        tp.tableData.splice(0, tp.tableData.length);
        var status=JSON.parse(msg[0]).status;

        if(status=="Error"){
				tp.$message({
					message:"后台错误",
					type:"error"
				})
			}else if(status=="Success"){
				tp.$message({
					message:"获取数据成功",
					type:"success"
        });
        
          var mydata_str=JSON.parse(msg[0]).mydata;
          var mydata=JSON.parse(mydata_str);
          console.log(mydata);
        for (var x in mydata) {
          var obj = mydata[x].docu_time;
          console.log(obj)
          var docu_data = tp.buquan(
            obj.year,
            obj.month+1,
            obj.dayOfMonth
          );
          var name = mydata[x].docu_name.toString();
          var lei_id = name.lastIndexOf(".");
          tp.tableData.splice(0, 0, {
            docu_data: docu_data,
            docu_name: mydata[x].docu_name,
            docu_cata: mydata[x].docu_cata,
            docu_sz: (parseInt(mydata[x].docu_sz)/1024/1024).toFixed(2).toString()+"M",
            docu_lei:name.substring(lei_id+1)
        
          });
        }
			
			}else{
				this.$message({
					message:"其他错误",
					type:"error"
				})
			}







      });
    }
  },
  created() {
    var tp = this;
    this.updatepage();
    if (this.$route.query.page != null)
      this.currentpage = parseInt(this.$route.query.page);
    else this.currentpage = 1;
    tp.changeinfo();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .container{
    padding: 100px;
  }
  .text-right{
    text-align: right;
  }
  .text-center{
    text-align: center;
  }
</style>



