<template>
	<div class="container">
		<h1 class="textcenter">ALL READING TESTS</h1>
		<div v-show="loading" class="textcenter">
			<el-button icon="el-icon-loading" 
		            	circle></el-button>
			<h4 class="textcenter">loading...waiting...</h4>
		</div>
		<div class="list">
			<el-input v-model="search" placeholder="输入材料标题快速查询"></el-input>
			<div v-for="read in filteredreadings" v-bind:key="read.reading_id">
			<el-card class="box-card" shadow="hover">
			    <div class="textleft testtitle">
			    	<router-link v-bind:to="'/read/oneread/'+read.reading_id">
			    		{{read.title}}
			   	 </router-link>
			    </div><br/>
			    <div class="textright subtitle">--created by{{read.author}}</div>
			</el-card>
			</div>
		</div>
		
	</div>
</template>

<script>
import axios from 'axios'
export default{
	name:'test-list',
	data(){
		return{
			readings:[],
			search:"",
			loading:true
		}
	},
	created(){
		//axios.get("https://listest-da6c5.firebaseio.com/reading.json")
	//	axios.post("http://localhost:8080/dhu/reading_gtall")
		axios.post("http://47.96.87.249:8080/dhuf/reading_gtall")
		.then(function(data){
			return data.data;
		})
		.then((data)=>{
			var msg=data;
			var status=JSON.parse(msg[0]).status;
                 console.log(status);
				   if(status=="Error"){
						this.$message({
							message:"后台错误",
							type:"error"
						})
					}else if(status=="Success"){
						this.$message({
							message:"获取成功",
							type:"success"
						});
						var testArray=[];
						var mydata=JSON.parse(msg[0]).mydata;
						testArray=JSON.parse(mydata);
						console.log("!!!"+testArray);
						
						this.readings=testArray;
						console.log(this.readings[0].title);
					}else {
						this.$message({
							message:"其他错误",
							type:"error"
						})
					}


			/*var testArray=[];
			for(let key in data){
				data[key].id = key;
				testArray.push(data[key]);
			}
			this.readings=testArray;*/
			this.loading=false;
		})
	},
	computed:{
		filteredreadings:function(){
			return this.readings.filter((tes)=>{
				return tes.title.match(this.search);
			})
		}
	}
}
</script>

<style scoped>
	.container{
		margin-top: 100px;
	}
	.textcenter{
		text-align: center;
	}
	.list{
		margin-top: 3em;
		margin-bottom: 10em;
		margin-right: 10em;
		margin-left: 10em;
	}
	.textright{
		text-align: right;
	}
	.textleft{
		text-align: left;
	}
	.testtitle{
		font-size: 16px;
		font-weight: bold;
	}
	.subtitle{
		color: #999999;
		font-size: 10px;
	}
	a{
		text-decoration: none;
		color: #666666;
	}
</style>




