<template>
  <div class="container">
    <h2 style="text-align:center">all文件管理</h2>
<el-row :gutter="20">
  <el-col :span="7"><div class="grid-content bg-purple"></div></el-col>
  <el-col :span="7">
     <el-upload
    class="upload-demo"
    drag
    :on-success="onsuccess"
    name="file"
    action="abc"
    :before-upload="beforeAvatarUpload"
    multiple
  >
    <i class="el-icon-upload"></i>
    <div class="el-upload__text text-center">
      将文件拖到此处，或
      <em>点击上传</em>
    </div>
    <!-- <div class="el-upload__tip text-center" slot="tip">只能上传jpg/png文件，且不超过500kb</div> -->
  </el-upload>
    </el-col>
  <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
</el-row>

  <!-- <input type="button" value="hhh" v-on:click="hehe" class="text-center"> -->

  <el-table
  v-loading="loading"
    element-loading-text="上传中"
    element-loading-spinner="el-icon-loading"
    :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%"
    :default-sort = "{prop: 'docu_data', order: 'descending'}"
  >
    <el-table-column sortable label="日期" prop="docu_data"></el-table-column>
    <el-table-column label="名字" prop="docu_name"></el-table-column>
    <el-table-column sortable label="类别" prop="docu_lei"></el-table-column>
    <el-table-column label="大小" prop="docu_sz"></el-table-column>
    <el-table-column align="left">
      <template slot="header">
        <el-input v-model="search" size="mini" placeholder="输入关键字搜索"/>
      </template>
      <template slot-scope="scope">
        <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">下载</el-button>
        <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <br>

  <el-pagination class="text-right"
    background
    layout="prev, pager, next"
    @size-change="sizechange"
    @current-change="currentchange"
    :page-size="pagesize"
    :total="total"
    :current-page="currentpage"
  ></el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      search: "",
      total: 0,
      pagesize: 5,
      currentpage: 2,
      loading: false
    };
  },
  methods: {
    async handleEdit(index, row) {
      console.log(index, row);

      /*var params=new URLSearchParams();
        params.append('docu_name',row.docu_name);
        var response=await this.$http({
          method:'get',
          url:'http://localhost:8080/dhu/docu_download',
          params
        });*/
      // this.$router.push('http://localhost:8080/dhu/docu_download?docu_name='+row.docu_name);
      //console.log(response);
      // location.href='http://localhost:8080/dhu/docu_download?docu_name='+row.docu_name;

      var params = new URLSearchParams();
      params.append("docu_name", row.docu_name);
      var response = await this.$http({
        method: "get",
        url: "http://47.96.87.249:8080/dhuf/docu_isexist",
     //  url: "http://localhost:8080/dhu/docu_isexist",
        params
      });
      var msg = response.data;
      var obj = JSON.parse(msg[0]);
      console.log(obj.status);
      if (obj.status == "success") {
        console.log("hello");
        location.href =
          "http://47.96.87.249:8080/dhuf/docu_download?docu_name=" + row.docu_name;
 //"http://localhost:8080/dhu/docu_download?docu_name=" + row.docu_name;
        this.$message({
          type: 'success',
          message: '下载成功'
        })
      } else {
        this.$message({
          type: 'error',
          message: '没有该文件哦'
        })
      }
      this.updatepage();
      this.changeinfo();
    },
    async handleDelete(index, row) {
      var tp = this;
      console.log("hahahahahha");
      this.$confirm("此操作将删除该文件，是否继续？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(async () => {
          var params = new URLSearchParams();
          params.append("docu_name", row.docu_name);
          //var response=await this.$htp.post('http://localhost:8080/dhu/docu_delete',params);
          var response = await tp.$http({
            method: "post",
            url: "http://47.96.87.249:8080/dhuf/docu_delete",
        //   url: "http://localhost:8080/dhu/docu_delete",
            params
          });
          console.log(response);
          var msg = response.data;
          for (var x in msg) {
            var obj = JSON.parse(msg[x]);
            console.log(obj.status);
            //success 为成功，notexistinfolder 为文件夹里面没有 notexistindb 为数据库里面没有
          }
          tp.updatepage();
          tp.changeinfo();
          if (obj.status != "success") throw "notexist";

          this.$message({
            type: "success",
            message: "删除成功"
          });
        })
        .catch(exception => {
          if (exception == "notexist") {
            this.$message.error("后台没有该文件orz");
          } else {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          }
        });
    },
    async hehe() {
      // console.log("hehe")
      //  this.$router.push({path:'/Test',query:{id:'123'}});
      //  console.log(this.$route.query.id)
      // console.log(this.currentpage)
      var temp = await this.test();
      console.log(temp);
      console.log("hi");
    },
    async test() {
      setTimeout(() => {}, 2000).then(() => {
        return "ok";
      });
    },
    onsuccess(response, file) {
      console.log("the upload:" + response);

      var msg = response;
      var obj = JSON.parse(msg[0]);
      console.log(obj.status);
      if (obj.status == "success") {
        this.$message({
          message: "宁上传成功",
          type: "success"
        });
      } else if (obj.status == "exist") {
        this.$message({
          message: "已经有这个文件了",
          type: "warning"
        });
      } else if (obj.status == "nofile") {
        this.$message({
          message: "后台没有接收到宁的消息哦",
          type: "warning"
        });
      } else {
        this.$message.error("未知的错误，请通知我");
      }

      this.updatepage();
      this.changeinfo();
    },
    buquan(year, month, day) {
      //console.log("year:"+year+"month:"+month+"day:"+day);
      var yearstr = year.toString();
      var monthstr = month.toString();
      var daystr = day.toString();
      if (monthstr.length == 1) monthstr = "0" + monthstr;
      if (daystr.length == 1) daystr = "0" + daystr;
      return yearstr + "-" + monthstr + "-" + daystr;
    },
    updatepage() {
      console.log("this is updatepage");
      var tp = this;
      var params = new URLSearchParams();
      params.append("docu_suffix", "mp4_avi_rmvb_wmv_flv_word_ppt_pdf_mp3_txt_jpg_jpeg");
      this.$http({
        method: "post",
        url: "http://47.96.87.249:8080/dhuf/docu_gecount_suf",
        params,

       // url: "http://localhost:8080/dhu/docu_gecount",
      
      }).then(function(response) {
        console.log(response.status);
        console.log(response.data);
        if (response.status == 200) {
          var msg = response.data;
          var obj = JSON.parse(msg[0]);
          console.log(obj.result);
          tp.total = obj.result;
        }
      });
    },
    sizechange(val) {
      console.log("size change");
      console.log(`每页 ${val} 条`);
    },
    currentchange(val) {
      console.log("current change");
      console.log(`当前页: ${val}`);
      this.currentpage = val;
      this.$router.push({ path: "/test", query: { page: val } });
     // this.$router.push({ path: "/teaindex/videomana", query: { page: val } });
      console.log(this.$route.query.page);
      this.updatepage();
      this.changeinfo();
    },
    changeinfo() {
      console.log("this is changeinfo");
      var tp = this;
      var params = new URLSearchParams();
      params.append("docu_suffix", "mp4_avi_rmvb_wmv_flv_word_ppt_pdf_mp3_txt_jpg_jpeg");
      params.append("begin", (this.currentpage - 1) * this.pagesize);
      params.append("pageSize", this.pagesize);
      this.$http({
        method: "post",
        url: "http://47.96.87.249:8080/dhuf/docu_geDocu_suf",
     // url: "http://localhost:8080/dhu/docu_geDocu_suf",
        params
      }).then(function(response) {
        console.log(response.data);
        var msg = response.data;
        tp.tableData.splice(0, tp.tableData.length);
        var status = JSON.parse(msg[0]).status;

        if (status == "Error") {
          tp.$message({
            message: "后台错误",
            type: "error"
          });
        } else if (status == "Success") {
          tp.$message({
            message: "获取数据成功",
            type: "success"
          });

          var mydata_str = JSON.parse(msg[0]).mydata;
          var mydata = JSON.parse(mydata_str);
          console.log(mydata);
          for (var x in mydata) {
            var obj = mydata[x].docu_time;
            console.log(obj);
            var docu_data = tp.buquan(obj.year, obj.month + 1, obj.dayOfMonth);
            var name = mydata[x].docu_name.toString();
            var lei_id = name.lastIndexOf(".");


            tp.tableData.splice(0, 0, {
              docu_data: docu_data,
              docu_name: mydata[x].docu_name,
              docu_cata: mydata[x].docu_cata,
              docu_sz: (parseInt(mydata[x].docu_sz)/1024/1024).toFixed(2).toString()+"M",
              docu_lei: name.substring(lei_id + 1)
            });
          }
        } else {
          this.$message({
            message: "其他错误",
            type: "error"
          });
        }
      });
    },
    beforeAvatarUpload(file) {
      this.loading=true;
      var tp = this;
      console.log("filename:" + file.name);
      var params = new URLSearchParams();
      params.append("docu_name", file.name);
      this.$http({
        method: "post",
       // url: "http://localhost:8080/dhu/docu_isexist",
       url: "http://47.96.87.249:8080/dhuf/docu_isexist",
        params
      }).then(function(response) {
        //console.log("~~~"+response.status);
        var msg = response.data;
        var status = JSON.parse(msg[0]).status;
        console.log("status:"+status);
        if (status == "notexistindb") {
          let fd = new FormData();
         
          fd.append("file", file);
          tp.$http
          //  .post("http://localhost:8080/dhu/docu_upload", fd)
           .post("http://47.96.87.249:8080/dhuf/docu_upload", fd)
            .then(function(res) {
              if(res.status==200){
              console.log("jump into onsuccess")
              tp.onsuccess(res.data, file);
              tp.loading=false;
              }else{
                tp.$message.error("网络错误");
                tp.loading=false;
              }
            });
        } else if (status == "notexistinfolder"||status=="success") {
          tp.$message({
            message: "已经有这个文件了",
            type: "warning"
           
          }); 
          tp.loading=false;
        } else {
          tp.$message.error("未知的错误，请通知我");
          tp.loading=false;
        }
      });
    }
  },
  created() {
    var tp = this;
    this.updatepage();
    if (this.$route.query.page != null)
      this.currentpage = parseInt(this.$route.query.page);
    else this.currentpage = 1;
    console.log("begin:" + (tp.currentpage - 1) * tp.pagesize);
    console.log("pageSize:" + tp.pagesize);
    tp.changeinfo();
  },
  watch: {
    currentpage(newValue, oldValue) {
      console.log("hello" + newValue);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .container{
    background: #f9fafc;
    border-radius:20px;
    padding: 20px;
    margin-bottom:60px;
    margin-right: 60px;
    /* margin-top: 60px;
    margin-bottom: 60px;
    margin-left: 40px;
    margin-right: 40px; */
  }
  .text-right{
    text-align: right;
  }
  .text-center{
    text-align: center;
  }
    .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>



