<template>
  <div v-theme:column="'narrow'" id="show-blog">
    <h2 class="textcenter">TOPICS讨论</h2>
    
    <input type="text" v-model ="search"  placeholder="快速搜索标题">
    <div v-for="blog in filteredBlogs" class="single-blog">
      <router-link v-bind:to = "'/forum/article/' + blog._id" >
    	<h3 class="textcenter">
              {{blog.title | toUpperCase}}
    	</h3>
        <div class="subtitle textcenter">
			    作者：{{blog.author}}&nbsp;&nbsp;&nbsp;时间:{{getdate(blog.date)}}
		    </div>
    
    	<article>
        {{change(blog.content).slice(0,20)}}
    		<!-- {{blog.content | snippet }} -->
    	</article>
      </router-link>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'show-blog',
  data(){
  	return{
  		blogs:[],
      search:""
  	}
  },
  methods:{
			getdate(ok){
				var id = ok.lastIndexOf('.');
				ok = ok.substring(0,id);
				return ok;
      },
      change(ok){
        var str = ok;
        str=str.replace(/<.*?>/g,"");
        // console.log(str);
        return str;
      }
  },
  created(){
  	// this.$http.get('http://jsonplaceholder.typicode.com/posts')
  	//this.$http.get('./../static/posts.json')
    // this.$http.get('https://vueblog-10728.firebaseio.com/posts.json')
     axios.get('http://47.100.60.171:5000/api/blogs/list')
  	.then(function(data){
         return data.data.blog;
  	})
     .then((data)=>{
      // console.log('here first');
      // console.log(data);
       var blogarray=[];
       for(var ind in data){
        //  console.log(data[ind]);
        if(data[ind].hidename==true)data[ind].author="secret";
         var tmpdata=data[ind].categories;
         for(var cat in tmpdata){
           if(tmpdata[cat]=="TOPICS讨论")
           blogarray.push(data[ind]);
         }
       }
       this.blogs=blogarray;
     })
  },
  computed:{
    filteredBlogs:function(){
      return this.blogs.filter((blog)=>{
        return blog.title.toUpperCase().match(this.search);
      })
    }
  },
  filters:{
    toUpperCase(value){
      return value.toUpperCase();
    }
  }
}
</script>

<style>
#show-blog{
	max-width: 800px;
	margin: 0 auto;
    background-image: url('../../assets/bg5.jpeg');
    background-attachment: fixed;
}
.single-blog{
	padding: 15px;
	margin:20px 0;
	box-sizing: border-box;
	/* background:#eee */
  background:  rgba(255,255, 255, 0.8);;
  border: 1px dotted #aaa;
}

#show-blog a{
  color: #444;
  text-decoration: none;
}

input[type="text"]{
  padding: 6px;
  width: 100%;
  box-sizing: border-box;
}
	.textcenter{
		text-align: center;
	}
  .subtitle{
    font-size: 13px;
  }
  article{
    background:rgba(255,255, 255, 0.8);
    padding: 10px;
    margin-top: 5px;
    border-radius: 5px;
  }
</style>



