<template>
    <div class="person">
        <el-tabs type="border-card">
            <el-tab-pane label="回复我的">
                <div v-for="(rr,ind) in replytome" :key=ind>
                    <el-link type="warning" v-if="rr.status==0" v-on:click="hasread(ind)">未读(点击标为已读)</el-link><br>
                    <nobr style="color:#6699CC;">@{{rr.reusername}}</nobr>
                        在
                        <router-link v-bind:to="'/forum/article/' + rr.blogid" >
                        {{rr.blogname}}</router-link>
                        中回复我：<br>
                        <p>
                            <el-row :gutter="20">
                                <el-col :span="16" :offset="3">
                                    <div style="font-weight:200;font-size:14px">
                                        {{rr.content}}
                                    </div>
                                    </el-col>
                                <el-col :span="2"> <nobr style="font-size:12px; color:grey; font-weight:600;">{{getdate(rr.date)}}</nobr>
                                </el-col>
                            </el-row>
                           
                        </p>
                     <el-divider></el-divider>
                </div>
            </el-tab-pane>
            <!-- <el-tab-pane label="赞我的">
赞我
            </el-tab-pane> -->


            <el-tab-pane label="我的帖子">
                <div v-for="(blog,ind) in myblog" :key="ind" class="single-blog">
                    <div v-show="notdel[ind]">
                        <router-link v-bind:to = "'/forum/article/' + blog._id" style="text-decoration:none"><h3 class="textcenter">
                            {{blog.title}}
                        </h3></router-link>
                        <div class="subtitle">
                            作者：{{blog.author}}&nbsp;&nbsp;&nbsp;时间:{{getdate(blog.date)}}
                        </div>
                        <article>
                        <!-- <div v-html="blog.content.slice(0,20)"/> -->
                       {{change(blog.content).slice(0,20)}}
                            <!-- {{blog.content | snippet }} -->
                       <el-row>
                           <el-col :offset="20">
                               <el-button type="primary" icon="el-icon-edit" circle @click="edit(blog._id)"></el-button>
                               <el-button type="danger" icon="el-icon-delete" circle v-on:click="del(blog._id,ind)"></el-button>
                            </el-col>
                       </el-row>
                       </article>
                       
                       <el-divider></el-divider>
                       </div>
                </div>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            replytome:[],
            myblog:[],
            notdel:[]
        }
    }
    ,
    created(){
        axios.get('http://47.100.60.171:5000/api/message/list/'+this.$route.params.id)
			.then(function(data){
                return data.data;
			})
			.then((data)=>{
                // console.log(data);
                this.replytome=data.message.reverse()
                // console.log(this.replytome)
            });

        axios.get('http://47.100.60.171:5000/api/blogs/mylist/'+this.$route.params.id)
            .then(function(data){
                return data.data;
			})
			.then((data)=>{
                // console.log(data);
                this.myblog=data.blog.reverse();
                for(var i=0;i<this.myblog.length;i++){
                    this.notdel[i]=true;
                }
                console.log(this.notdel);
                // console.log(this.myblog)
            });
    },
    methods:{
        getdate(ok){
				if(!ok)return ok;
				var id = ok.lastIndexOf('.');
				ok = ok.substring(0,id);
				return ok;
			},
      change(ok){
        var str = ok;
        str=str.replace(/<.*?>/g,"");
        // console.log(str);
        return str;
      },
      del(blogid,ind){
          if(confirm('确认要删除么')){
              alert(blogid)
              var ok = {del_blogid:blogid};
              axios.post('http://47.100.60.171:5000/api/blogs/del',ok).then(
                  (data)=>{
                      console.log(ind);
                      console.log(data);
                      alert('删除成功')
                  }
              )
          }
      },
      edit(blogid){
          this.$router.push({path:'/forum/edit/'+blogid});
      },
      hasread(id){
          this.replytome[id].status=1;
          axios.post('http://47.100.60.171:5000/api/message/hasread/'+this.$route.params.id+'/'+this.replytome[id]._id)
          .then((data)=>{
              console.log(data);
          })
        //   alert('here')
      }
    }
}
</script>

<style scoped>
.single-blog{
    font-size: 14px;
}
.textcenter{
    color:#333333;
    text-align: center;
    font-weight: 600;
    font-size: 20px;
}
.subtitle{
    text-align: center;
    color: gray;
}
article{
    color:black;
}
</style>




