<template>
    <div class="container">
        <el-container class="container">
        <el-aside>
            <div class="leftnav">
            <ul class="pure-menu-list" >
                <li class="pure-menu-heading"><br><b>Mini Forum 迷你论坛</b><br><br></li>
                <li class="pure-menu-item textcenter wow"><a href="/dhu/forum/lists" class="pure-menu-link">LIST 首页</a></li>
                <li class="pure-menu-item textcenter wow"><a href="/dhu/forum/topic" class="pure-menu-link">TOPICS 讨论</a></li>
                <li class="pure-menu-item textcenter wow"><a href="/dhu/forum/qanda" class="pure-menu-link">Q&A 问答</a></li>
                <li class="pure-menu-item textcenter wow"><a href="/dhu/forum/secret" class="pure-menu-link">SECRET 树洞</a></li>
                <li class="pure-menu-item textcenter wow"><a href="/dhu/forum/add" class="pure-menu-link">POST 发帖</a></li>

                <router-link v-bind:to = "'/forum/message/' + username" style="text-decoration:none" >
                <li v-show="logged" class="pure-menu-item textcenter wow"><a class="pure-menu-link"><el-badge is-dot class="item">个人中心</el-badge></a></li>
                </router-link>
            </ul>
            </div>
        </el-aside>
        <el-main>
            <router-view></router-view>
        </el-main>
        </el-container>
    </div>
</template>

<script>
import onearticle from './onearticle'
export default {
    data(){
        return{
            logged:false,
            username:''
        }
    },
    components:{
        "app-article":onearticle
    },
    created(){
        this.check();
    },
    
    methods:{
    check(){
      var tp=this;
      this.$http({
        method:"post",
        url:"http://47.96.87.249:8080/dhuf/user_test"
      }).then(function(response){
        var msg=response.data;
        var obj=JSON.parse(msg[0]);
        if(obj.status=="have"){
          tp.logged=true;
          tp.username=obj.name;
        }
      });
     }
    }
    
}
</script>

<style scoped>
.container{
    margin-top:20px;
    margin-bottom: 0px;
    min-height: 500px;
    height: 100%;
    margin-left: 0px;
    margin-right:0px;
    background-image: url('../../assets/bg5.jpeg');
    background-attachment: fixed;
}
  .leftnav{
    top:70px;
    width: 220px;
    height: 600px;
    margin-left: 30px;
    border-radius: 20px;
    background-color:rgba(255,255, 255, 0.8);
    color:salmon;
    position: fixed !important;
  }
  .textcenter{
      text-align: center;
  }
  .wow :visited{
      background-color:rgba(255,255, 255, 0.6);
      font-weight: 500;
  }
</style>




