<template>
	<div id="single-blog">
		<h2 class="textcenter">{{oneblog.title}}</h2>
		<div class="subtitle textcenter">
			作者：{{showauthor}}&nbsp;&nbsp;&nbsp;时间:{{getdate(oneblog.date)}}
		</div>
		<div class="ql-container ql-snow">
          <div class="ql-editor">
            <article v-html="oneblog.content"></article>
          </div>
      </div>
		
		<p></p>
		<h5>类别：
				<tag  v-for="(category,index) in oneblog.categories" :key="index">
					{{category}}&nbsp;&nbsp;
				</tag>			
		</h5>

		
		<div class="reviewlist" v-if="review!=null">
			<div class="floor" v-for="(rr,index) in review.reviewlist" :key="index">
				<div class="onefloor">
					<p style="font-size:15px;font-weight:200;">
						<nobr style="font-weight:600;color:gray;">floor {{index+1}}:</nobr>
						<nobr style="color:#6699CC;">@{{rr.hostname}}</nobr>
						<nobr style="font-size:12px; color:grey; font-weight:600;">{{getdate(rr.date)}}</nobr>
					</p>
					<p>{{rr.hostcontent}}</p>
					<el-row>
						<el-col span="2" :offset="20">
							<el-link type="danger" v-on:click="showReply(rr.hostname,index)">回复ta</el-link>
						</el-col>
					</el-row>
				</div>
				<div class="infloor" v-if="rr.reviewitem.length > 0" v-for="(inf,ind) in rr.reviewitem" :key="ind">
					
					<nobr style="color:#6699CC;">@{{inf.curname}} </nobr>
					回复
					<nobr style="color:#6699CC;">@{{inf.oldname}}</nobr>
					:{{inf.curcontent}}
					<nobr style="font-size:12px; color:lightgrey; font-weight:600;">{{getdate(inf.date)}}</nobr>
					<el-row>
						<el-col span="2" :offset="20">
							<el-link type="danger"  v-on:click="showReply(inf.curname,index)">回复ta</el-link>
						</el-col>
					</el-row>
					
					</div>
					<div class="replybox" v-if="currentindex === index && logged">
						<!-- <el-input
  							placeholder="请输入回复者姓名"
	  						v-model="rcurname">
						</el-input> -->
						回复<nobr style="color:#6699CC;">@{{roldname}}</nobr>
						<el-row>
							<el-col :offset="2" span="18">
								<el-input
									type="textarea"
									:rows="4"
									placeholder="请输入回复内容"
									v-model="rcurreview">
								</el-input>
							</el-col>
						</el-row>
						<el-row>
							<el-col :span="2" :offset="20"> 
								<el-button type="danger" size="small"  round plain v-on:click="commitReply(index)">提交评论</el-button>
							</el-col>
						</el-row>
						 
					</div>
					<br>
					<br>
			</div>
		</div>

		  <el-divider style="color:pink" >新的一层!~来给{{showauthor}}回复鸭</el-divider>
		<div class="newfloor" v-show="logged">
			<el-input
				type="textarea"
				:rows="4"
				placeholder="请输入回复"
				v-model="curreview">
			</el-input>
			<!-- <el-input
				placeholder="请输入回复者姓名"
				v-model="curname">
			</el-input> -->
			<br>
			<el-row :gutter="20" style="margin-top:10px;">
				<el-col :span="2" :offset="20">
					<el-button  size="small" round type="danger" v-on:click="addreview()">提交评论</el-button>
				</el-col>
			</el-row>
		</div>
		
  <div class="notlog" v-show="!logged">
    <h3 class="textcenter">!!!请登录后再进行评论</h3>
  </div>
		
		<!-- <button @click="deleteSingleBlog()">删除 </button> -->
		<!-- <router-link :to=" '/vueblog/edit/' + id ">编辑</router-link> -->
	</div>
</template>

<script>
	import axios from 'axios'
	import 'quill/dist/quill.core.css';
	import 'quill/dist/quill.snow.css';
	import 'quill/dist/quill.bubble.css';
	export default{
		name:"single-blog",
		data(){
			return {
				id:this.$route.params.id,
				oneblog:{},
				review:{},
				curreview:'',
				curname:'',

				roldname:'',
				rcurname:'',
				rcurreview:'',
				currentindex:-1
			}
		},
		created(){
			this.check();
            axios.get('http://47.100.60.171:5000/api/blogs/findone/'+this.$route.params.id)
			.then(function(data){
                return data.data;
			})
			.then((data)=>{
                var msg=data.blog;
                // console.log(msg);
				this.oneblog=msg;
				if(this.oneblog.hidename==true)this.showauthor="secret";
				//alert(typeof(this.oneblog.date));
				// this.oneblog.date = new Date(this.oneblog.date);
				// alert(date);
				// console.log('blog success');
            })

			axios.get('http://47.100.60.171:5000/api/review/list/'+this.$route.params.id)
			.then(function(data){
				// console.log(this.$route.params.id);
				// console.log(data);
				return data.data;
			})
			.then((data)=>{
				var onereview=data.review;
				// console.log(onereview);
				this.review=onereview;
			})
			
		},
		methods:{
			deleteSingleBlog(){
				this.$http.delete("https://vueblog-10728.firebaseio.com/posts/"+this.id+".json")
				.then(response => {
					this.$router.push({path:'/'})
				})
			},
			addreview(){
				var tijiao={
					blogid:this.id,
					hostname:this.curname,
					hostcontent:this.curreview
				};
				console.log(tijiao);
				axios.post('http://47.100.60.171:5000/api/review/addone',tijiao)
				.then((data)=>{
					console.log(data);
				})
			},
			showReply:function(name,index){
				if(this.currentindex != index){
					this.currentindex = index;
					this.roldname=name;
				}
				else{
					this.roldname=name;
				}
			},
			commitReply:function(index){
				var tijiao={
					blogid:this.id,
					hostid:this.review.reviewlist[index]._id,
					oldname:this.roldname,
					curname:this.rcurname,
					curcontent:this.rcurreview
				};
				// console.log(tijiao);
				axios.post('http://47.100.60.171:5000/api/review/replyreply',tijiao)
				.then((data)=>{
					this.$message({
						message: "评论成功，刷新查看",
						type: "success"
					});
					// console.log(data);
				})
			},
			getdate(ok){
				if(!ok)return ok;
				var id = ok.lastIndexOf('.');
				ok = ok.substring(0,id);
				return ok;
			},
			check(){
			var tp=this;
			this.$http({
				method:"post",
				url:"http://47.96.87.249:8080/dhuf/user_test"
			//  url:"http://192.168.1.107:8080/dhu/user_test"
			}).then(function(response){
				var msg=response.data;
				var obj=JSON.parse(msg[0]);
				console.log("now:"+obj.status);
				if(obj.status=="have"){
					tp.logged=true;
					tp.curname=obj.name;
					tp.rcurname=obj.name;
				}
			});
			}
		}
	}
</script>

<style>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background-color: white;
		border: 1px dotted #aaa;
		border-radius: 20px;
	}
	article{
		background: #eee;
		min-height: 50px;
		margin-top:10px;
		padding: 30px;
		border-radius: 30px;
	}
	.textcenter{
		text-align: center;
	}
	tag{
		padding: 10px;
		margin-left: 20px;
		background: pink;
		border-radius: 20px;
	}
	.floor{
		/* border: 1px dotted #aaa; */
		min-height: 50px;
		border-width: 1px;
		border-color:palevioletred;
		border-style:solid none none none;
		padding: 10px;
		font-weight: 300;
	}
	.onefloor{
		background-color:rgba(204, 204, 204, 0.15);
		border-radius:10px;
		border-bottom-color: antiquewhite;
		border-bottom-style: solid;
		border-bottom-width: 1px;
		font-weight: 400;
		padding: 10px;
	}
	.infloor{
		margin-left: 50px;
		padding:10px;
		font-size: 13px;
		font-weight: 400;
		border-bottom-color: antiquewhite;
		border-bottom-style: solid;
		border-bottom-width: 1px;
	}
	.replybox{
		margin-left: 10px;
		margin-top: 5px;
		font-size: 14px;
	}
	.notlog{
		margin: 10px;
		padding: 30px;
		background: rgba(255,255,255,0.4);
		color:slateblue;
		}
		img{
		height: 100px;
		width: 100px;
		}
</style>


