<template>
<div class="container">

   <div id="add-blog">
    <h2>编辑帖子</h2>
    <form v-if="!submitted" class="addblog">
      <label>标题</label>
      <input type="text" v-model="blog.title" reguired/>
      <label>内容</label>
      <div class="econtainer">
        <quill-editor 
            v-model="blog.content" 
            ref="myQuillEditor" 
            @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
            @change="onEditorChange($event)">
        </quill-editor>
      </div>
      <div id="checkboxes">
        <label>Q&A问答</label>
        <input type="checkbox" value="Q&A问答" v-model="blog.categories">
        <label>TOPICS讨论</label>
        <input type="checkbox" value="TOPICS讨论" v-model="blog.categories">
        <label>OTHER其他</label>
        <input type="checkbox" value="OTHER其他" v-model="blog.categories">
      </div>
           <label>是否匿名（匿名会加入树洞中）：
           <input type="radio" name="hidename" value="false" v-model="blog.hidename">不匿名
           <input type="radio" name="hidename" value="true" v-model="blog.hidename">匿名</label>
      
    </form>

    <div v-if="submitted">
      <h3>您的博客修改成功</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <div class="ql-container ql-snow">
          <div class="ql-editor">
            <div v-html="blog.content"></div>
          </div>
      </div>
      <p>标签分类：
      <ul>
        <nobr style="margin-left:10px;background: pink; border-radius: 20px;" v-for="(category,ind) in blog.categories" :key="ind">
          {{category}}&nbsp;&nbsp;
        </nobr>
      </ul>
      </p>
      <button id="btn2" v-on:click="post" v-if="!submitted">修改博客</button>
    </div>
  </div>
  </div>
</template>

<script>
import axios from 'axios'
import { quillEditor } from "vue-quill-editor"; //调用编辑器
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
export default {
  name: 'add-blog',
  components:{
    quillEditor
  },
  data () {
    return {
      blog:{
      },
      submitted:false,
    }
  },
  props: {
    defaultMsg: {
        type: String
     },
    config: {
        type: Object
    }
  },
  mounted(){
    const _this = this;
      this.editor = add-blog.getEditor('editor', this.config); // 初始化UE
      this.editor.addListener("ready", function () {
        _this.editor.setContent(_this.defaultMsg); // 确保UE加载完成后，放入内容。
      });
  },
  created(){
    axios.get('http://47.100.60.171:5000/api/blogs/findone/'+this.$route.params.id)
			.then(function(data){
                return data.data;
			})
			.then((data)=>{
                var msg=data.blog;
				this.blog=msg;
            })
  },
  computed: {
        editor() {
            return this.$refs.myQuillEditor.quill;
        },
  },
  methods:{
    post:function(){    
        axios.post("http://47.100.60.171:5000/api/blogs/edit",this.blog)
        .then((data)=>{
          this.submitted = true;
          console.log(data);
        });
    }
    ,
    preview(){
      alert(this.blog.content);
    },
        onEditorBlur(){}, // 失去焦点事件
        onEditorFocus(){}, // 获得焦点事件
        onEditorChange(){}, // 内容改变事件
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
}
#add-blog{
  margin: 20px auto;
  max-width: 800px;
  padding: 20px;
}
label{
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px;
}
textarea{
  height: 200px;
}
#checkboxes label{
  display: inline-block;
  margin-top: 0;
}
#checkboxes input{
  display: inline-block;
  margin-right: 10px;
}
button{
  display: block;
  margin: 20px 0;
  background-color: salmon;
  color: #fff;
  border:0;
  padding: 16px;
  border-radius: 24px;
  font-size: 18px;
  cursor: pointer;
}

#preview{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
    background-color: rgba(255,255,255,0.4);
}
h3{
  margin-top: 10px;
}
.notlog{
  margin: 100px;
  padding: 30px;
  background: rgba(255,255,255,0.4);
  color:slateblue;
}
.econtainer{
      background-color: rgba(255,255,255,0.4);
      min-height: 400px;
}
img{
  height: 100px;
  width: 100px;
}
</style>



